/*------------------------------------*\
    RESET
\*------------------------------------*/
/* http://meyerweb.com/eric/tools/css/reset/ 
    v2.0b1 | 201101 
    NOTE:WORK IN PROGRESS
    USE WITH CAUTION AND TEST WITH ABANDON */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

/* remember to define visible focus styles! 
:focus{
    outline:?????;
} */
/* remember to highlight inserts somehow! */
ins {
  text-decoration: none; }

del {
  text-decoration: line-through; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

#app {
  display: grid;
  grid-template-rows: repeat(5, auto);
  grid-template-columns: repeat(8, 1fr); }

.logo {
  grid-column-start: 1;
  grid-row-start: 1; }

nav {
  grid-column-start: 5;
  grid-column-end: 8;
  grid-row-start: 1;
  display: grid;
  grid-auto-columns: 1fr; }
  nav a {
    align-self: flex-end;
    grid-column-start: 1; }
  nav a#work {
    grid-column-start: 2; }
  nav a#contact {
    grid-column-start: 3; }

.sidebar {
  grid-row-start: 2;
  grid-row-end: 4; }

section {
  display: grid;
  grid-column: 2 / 9;
  grid-template-columns: repeat(7, 1fr);
  grid-row-gap: 30px; }
  section header {
    grid-column: 1 / 5; }

section#me {
  grid-row-start: 2; }
  section#me article {
    grid-row-start: 2;
    grid-column-start: 4;
    grid-column-end: 8; }

section#work {
  grid-row-start: 3; }
  section#work article {
    grid-column: 1 / 8;
    grid-template-columns: repeat(7, 1fr);
    display: grid; }
    section#work article div {
      grid-column-start: 1;
      grid-column-end: 3; }
    section#work article img {
      grid-column-start: 4;
      grid-column-end: 8; }
  section#work article#project-01 {
    grid-row-start: 2; }
  section#work article#project-02 {
    grid-row-start: 3; }
  section#work article#project-03 {
    grid-row-start: 4; }

section#contact {
  grid-template-columns: repeat(8, 1fr);
  grid-row-start: 7;
  grid-column: 1 /9; }
  section#contact header {
    grid-column: 2 / 5;
    display: grid; }
  section#contact p {
    grid-row-start: 2;
    grid-column: 2 / 6; }
  section#contact footer {
    grid-row-start: 3;
    grid-column: 2 / 8; }

body {
  background-image: url("/public/grid.png");
  background-position: center;
  font-family: 'IBM Plex Sans';
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  font-size: 24px;
  color: #051429;
  color: var(--night-blue);
  text-align: left;
  line-height: normal; }

#app {
  margin: 0 33px; }

header {
  position: relative;
  height: 128px; }
  header div.rectangle {
    position: absolute;
    background-color: #183eef;
    top: 70px;
    z-index: -1;
    height: 50%;
    width: 100%; }
  header h1 {
    font-size: 95px;
    font-weight: bold;
    line-height: 1.29;
    letter-spacing: -0.5px;
    z-index: 1; }

h4 {
  font-weight: bold; }

h3 {
  font-size: 36px;
  font-weight: bold;
  letter-spacing: -0.2px;
  margin-bottom: 49px; }

img {
  width: 100%; }

p {
  opacity: 0.8;
  text-align: justify;
  color: #051429;
  color: var(--night-blue);
  line-height: 2;
  letter-spacing: -0.1px; }

a {
  font-size: 18px;
  font-weight: bold;
  color: #183eef; }



